.navbar {
    background: #333333;
    padding: 1rem 2rem;
    position: fixed;
    width: 100%;
   
    border-bottom: 1px #e18145 solid;
    z-index: 1000;
  }
  
  .navContainer {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navBrand h1 {
    color: #e18145;
    font-size: 1.8rem;
    margin: 0;
  }
  
  .navBrand span {
    color: #999999;
    font-size: 1rem;
  }
  
  .navMenu {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .navMenu li {
    margin-left: 2rem;
  }
  
  .navMenu a {
    color: #ffffff;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease;
  }
  
  .navMenu a:hover,
  .navMenu a.active {
    color: #e18145;
  }
  
  .navToggle {
    display: none;
    flex-direction: column;
    gap: 0.4rem;
    cursor: pointer;
  }
  
  .navToggle span {
    width: 25px;
    height: 3px;
    background: #ffffff;
    transition: all 0.3s ease;
  }
  
  @media (max-width: 768px) {
    .navMenu {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 70px;
      left: 0;
      width: 100%;
      background: #333333;
      padding: 1rem;
    }
  
    .navMenu.active {
      display: flex;
    }
  
    .navMenu li {
      margin: 1rem 0;
    }
  
    .navToggle {
      display: flex;
    }
  }


  