.footer {
  background-color: #1c2526;
  border-top: 1px solid #2e3a3b;
  position: relative;
  overflow: hidden;
}

.footer__gradient {
  position: absolute;
  inset: 0;
  background-image: linear-gradient(to right, #2e291b, #000000, #3b372b);
  opacity: 0.8;
}

.footer__gradientPrimary {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-image: linear-gradient(to right, #383838, #ff9900);
}

.footer__container {
  position: relative;
  max-width: 768px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.footer__grid {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 2rem;
}

@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (min-width: 1024px) {
  .footer__grid {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

.footer__company {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer__companyTitle {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.footer__companyTitleGradient {
  background-image: linear-gradient(to right, #f97316, #fb923c);
  -webkit-background-clip: text;
  color: transparent;
}

.footer__companyDescription {
  color: #8b9a9b;
  line-height: 1.625;
}

.footer__socialLinks {
  display: flex;
  gap: 1rem;
}

.footer__socialLink {
  width: 2.5rem;
  height: 2.5rem;
  background-color: #2e3a3b;
  border-radius: 9999px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.15s;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px -1px rgba(0, 0, 0, 0.1);
}

.footer__socialLink:hover {
  background-image: linear-gradient(to right, #f97316, #fb923c);
  transform: scale(1.1);
  box-shadow: 0 4px 6px -1px rgba(249, 115, 22, 0.1), 0 2px 4px -1px rgba(249, 115, 22, 0.06);
}

.footer__socialLinkIcon {
  color: #8b9a9b;
  transition-property: color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.15s;
}

.footer__socialLinkIcon:hover {
  color: #fff;
}

.footer__navigation {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer__navigationTitle {
  font-size: 1.125rem;
  font-weight: 600;
  color: #d4dfe0;
  margin-bottom: 1rem;
}

.footer__navigationList {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  list-style: none;
  padding: 0;
}

.footer__navigationLink {
  color: #8b9a9b;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.15s;
}

.footer__navigationLink:hover {
  color: #f97316;
  transform: translateX(0.25rem);
}

.footer__services {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer__servicesTitle {
  font-size: 1.125rem;
  font-weight: 600;
  color: #d4dfe0;
  margin-bottom: 1rem;
}

.footer__servicesList {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  list-style: none;
  padding: 0;
}

.footer__servicesItem {
  color: #8b9a9b;
  transition-property: color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.15s;
  cursor: default;
}

.footer__servicesItem:hover {
  color: #f97316;
}

.footer__contact {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer__contactTitle {
  font-size: 1.125rem;
  font-weight: 600;
  color: #d4dfe0;
  margin-bottom: 1rem;
}

.footer__contactInfo {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.footer__contactItem {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.footer__contactItem:hover .footer__contactIcon {
  transform: scale(1.1);
}

.footer__contactItem:hover .footer__contactText {
  color: #f97316;
}

.footer__contactIcon {
  color: #f97316;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.15s;
}

.footer__contactText {
  color: #8b9a9b;
  transition-property: color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.15s;
}

.footer__divider {
  border-top: 1px solid #2e3a3b;
  margin-top: 2rem;
  padding-top: 2rem;
}

.footer__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

@media (min-width: 768px) {
  .footer__bottom {
    flex-direction: row;
    justify-content: space-between;
    gap: 0;
  }
}

.footer__copyright {
  color: #8b9a9b;
  font-size: 0.875rem;
}

.footer__copyrightName {
  color: #f97316;
  font-weight: 500;
}