
.sidebar {
  position: fixed;
  top: 5rem;
  left: 0;
  width: 100%;
  height: calc(100vh - 5rem);
  background: #1c2526;
  transform: translateX(-100%);
  transition: transform 0.3s ease-in-out;
  z-index: 999;
}

a{
  text-decoration: none;
}

.sidebar--open {
  transform: translateX(0);
}

.sidebar--closed {
  transform: translateX(-100%);
}

.sidebar__nav {
  padding: 2rem;
}

.sidebar__list {
  list-style: none;
}

.sidebar__item {
  margin-bottom: 0.5rem;
}

.sidebar__link {
  display: flex;
  align-items: center;
  color: #ffffff;
  text-decoration: none;
  padding: 1rem;
  border-radius: 0.5rem;
  position: relative;
  border: 2px solid transparent;
  transition: color 0.2s ease, transform 0.3s ease, border 0.2s ease;
}

.sidebar__icon {
  margin-right: 0.5rem;
}

.sidebar__link:hover {
  border: 2px solid #00000033;
}

.sidebar__link--active {
  color: #ffffff;
  background: #2e3a3b;
  border: 2px solid #8d3e0581;
  animation: traveling-glow 2.5s infinite ease-in-out;
}

.sidebar__link--active::after {
  content: '';
  display: block;
  width: 0.6rem;
  height: 0.6rem;
  background-color: #f97316;
  border-radius: 50%;
  position: absolute;
  right: 1rem;
  animation: pulse-dot 2s infinite ease-in-out;
}

@keyframes traveling-glow {
  0% {
    border-color: #f97316;
    box-shadow: 0 0 5px rgba(249, 115, 22, 0.5);
  }

  100% {
    border-color: #f97316;
    box-shadow: 0 0 5px rgba(249, 115, 22, 0.5);
  }
}

@keyframes pulse-dot {
  0% {
    transform: scale(0.8);
    opacity: 1;
    background-color: #f97316;
  }
  25% {
    transform: scale(1.2);
    opacity: 0.7;
    background-color: #fb923c;
  }
  50% {
    transform: scale(1);
    opacity: 0.4;
  }
  75% {
    transform: scale(1.2);
    opacity: 0.7;
    background-color: #fb923c;
  }
  100% {
    transform: scale(0.8);
    opacity: 1;
    background-color: #f97316;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .sidebar {
    width: 30%;
    background: #2e3a3b;
    z-index: 1000;
    
  }

  .sidebar__nav {
    padding: 3rem;
  }

  .sidebar__link {
    padding: 1rem;
    font-size: 1rem;
  }

  .sidebar__link--active::after {
    right: 1.5rem;
  }
}

@media (min-width: 1025px) {
  .sidebar {
    position: fixed;
    top: 8rem;
    left: 0;
    width: 20%;
    height: calc(100vh - 8rem);
    background: #2e3a3b;
    transform: translateX(0);
    z-index: 1001;
    color: #ffffff;
  }

  .sidebar--open,
  .sidebar--closed {
    transform: translateX(0);
  }

  .sidebar__nav {
    padding: 3rem;
  }

  .sidebar__link {
    padding: 1.5rem;
    font-size: 1rem;
  }

  .sidebar__link--active::after {
    right: 1.5rem;
  }
}
